"use strict";

window.addEventListener('DOMContentLoaded', () => {

  const btns = document.querySelectorAll('[data-menu-btn]'),
        menusList = document.querySelectorAll('.menu'),
        burger = document.querySelector('.burger'),
        burgerBtn = document.querySelector('.header__categories-btn');

  btns.forEach(btn => {
    btn.addEventListener('click', function() {
      menusList.forEach(item => {
        if (btn.getAttribute('data-menu-btn') == item.id && !item.classList.contains('active')) {
          menusList.forEach(item => item.classList.remove('active'));
          burger.classList.add('active');
          item.classList.add('active');
        } else {
          if (btn.getAttribute('data-menu-btn') == item.id && item.classList.contains('active')) {
            item.classList.remove('active');
            burger.classList.remove('active');
          }
        }
      });
    });
  });

  burgerBtn.addEventListener('click', function() {
    if (burger.classList.contains('active')) {
      menusList.forEach(item => item.classList.remove('active'));
      burger.classList.remove('active');
    } else {
      document.getElementById('categories-menu').classList.add('active');
      burger.classList.add('active');
    }
  });

  const cartModal = document.querySelector('.cart-modal'),
        cartBtn = document.querySelector('.header__btn-mobile-cart');

  cartBtn.addEventListener('click', function() {
    this.classList.toggle('active');
    cartModal.classList.toggle('show');
    document.body.classList.toggle('no-scroll');
  });

  function cartModalShow() {
    cartModal.classList.add('show');
    document.body.classList.add('no-scroll');
  }

  function cartModalHide() {
    cartModal.classList.remove('show');
    document.body.classList.remove('no-scroll');
  }

  document.querySelector('.header__btn-cart').addEventListener('click', function() {
    cartModalShow();
  });

  document.querySelector('.cart-modal').addEventListener('click', function(e) {
    if (e.target.classList.contains('cart-modal')) {
      cartBtn.classList.remove('active');
      cartModalHide();
    }
  });

  document.querySelector('.cart-modal__close').addEventListener('click', function() {
    cartModalHide();
  });

  $('#introSlider').slick({
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: true,
    dotsClass: 'intro-slider__dots-container',
    centerMode: true,
    arrows: false,
    autoplay: true,
    autoplaySpeed: 10000,
    fade: true,
  });

  $('.tabs__content-slider').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    prevArrow: `<button class="products-slider__arrow products-slider__arrow-prev" aria-label="Previous" type="button">
                  <img src='assets/img/arrow-left-orange.svg' alt=''>
                </button>`,
    nextArrow: `<button class="products-slider__arrow products-slider__arrow-next" aria-label="Next" type="button">
                  <img src='assets/img/arrow-right-orange.svg' alt=''>
                </button>`
  });
  

  // function sliderInit() {
  //   sliderWidth = slider.offsetWidth + 10;
  //   sliderTrack.style.width = sliderWidth * sliderImages.length + 'px';
  //   sliderImages.forEach(img => {
  //     img.style.width = sliderWidth - 20 + 'px';
  //     img.style.height = 'auto';
  //   });
  // }

  // window.addEventListener('resize', sliderInit);
  // sliderInit();

  // function nextSlide() {
  //   slidesCount++;
  //   if (slidesCount >= sliderImages.length) {
  //     slidesCount = 0;
  //   }
  //   rollSlider();
  // }

  // function rollSlider() {
  //   sliderTrack.style.transform = `translate(-${slidesCount * (sliderWidth - 10)}px)`;
  //   activeDot();
  // }

  // let dotsContainer;
  // let sliderDots = [];

  // function createDots() {
  //   dotsContainer = document.createElement('div');
  //   dotsContainer.classList.add('slider__dots-container');
  //   slider.appendChild(dotsContainer);
  //   for (let i = 0; i < sliderImages.length; i++) {
  //     sliderDots[i] = document.createElement('button');
  //     sliderDots[i].classList.add('slider__dots-item');
  //     dotsContainer.appendChild(sliderDots[i]);
  //   }
  //   activeDot();
  // }

  // function activeDot() {
  //   sliderDots.forEach((dot) => {
  //     dot.classList.remove('active');
  //   });
  //   sliderDots.forEach((dot, i) => {
  //     if (i == slidesCount) {
  //       dot.classList.add('active');
  //     }
  //   });
  // }

  // createDots();

  // dotsContainer.addEventListener('click', (e) => {
  //   sliderDots.forEach((dot, i) => {
  //     if (e.target == dot) {
  //       slidesCount = i-1;
  //       nextSlide();
  //       activeDot();
  //     }
  //   });
  // });

  // setInterval(nextSlide, 8000);

  function tabs(tabsSelector, contentSelector) {
    const tabs = document.querySelectorAll(tabsSelector),
          content = document.querySelectorAll(contentSelector);

    tabs.forEach(tab => {
      tab.style.width = 100 / tabs.length + '%';
      tab.addEventListener('click', e => {
        e.preventDefault();
        tabs.forEach(item => {
          item.classList.remove('active');
        });
        e.target.classList.add('active');
        content.forEach(item => {
          item.classList.remove('active');
        });
        content.forEach(item => {
          if (e.target.getAttribute('href') == '#' + item.id) {
            item.classList.add('active');
            $('.tabs__content-slider').slick("refresh");
          }
        });
      });
    });
  }

  tabs('.tabs .tabs__item', '.tabs__container .tabs__content-item');

  $('.products-sliders__item-slider').slick({
    infinite: false,
    slidesToShow: 2,
    slidesToScroll: 1,
    mobileFirst: true,
    prevArrow: `<button class="products-slider__arrow products-slider__arrow-prev" aria-label="Previous" type="button">
                  <img src='assets/img/arrow-left-orange.svg' alt=''>
                </button>`,
    nextArrow: `<button class="products-slider__arrow products-slider__arrow-next" aria-label="Next" type="button">
                  <img src='assets/img/arrow-right-orange.svg' alt=''>
                </button>`,
    responsive: [
      {
        breakpoint: 896,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 2
        }
      },
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 3
        }
      }
    ]
  });

  const filters = document.querySelector('.filters');

  if (document.querySelector('.catalog__filters-btn')) {
    document.querySelector('.catalog__filters-btn').addEventListener('click', () => {
      filters.classList.add('active');
      document.body.classList.add('no-scroll');
    });
  }

  if (document.querySelector('.filters__close-btn')) {
    document.querySelector('.filters__close-btn').addEventListener('click', () => {
      filters.classList.remove('active');
      document.body.classList.remove('no-scroll');
    });
  }

  document.querySelectorAll('.filters__title').forEach(item => {
    item.addEventListener('click', function() {
      this.classList.toggle('active');
      this.nextElementSibling.classList.toggle('active');
    });
  });

  $(".js-range-slider").ionRangeSlider({
    type: "double",
    min: 0,
    max: 70000,
    from: 19000,
    to: 49000,
    grid: false,
    skin: "round",
    postfix: " руб"
  });

  if (document.querySelector('.catalog__sort-line')) {
    document.querySelector('.catalog__sort-line').addEventListener('click', () => {
      document.querySelector('.catalog__products').classList.add('catalog__products--list');
    });
  }

  if (document.querySelector('.catalog__sort-grid')) {
    document.querySelector('.catalog__sort-grid').addEventListener('click', () => {
      document.querySelector('.catalog__products').classList.remove('catalog__products--list');
    });
  }

  $('.product-page__images-slider').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    infinite: false,
    mobileFirst: true,
    prevArrow: `<button class="product-page__slider-arrow product-page__slider-arrow--prev" 
                        aria-label="Previous" type="button">
                  <img src='assets/img/arrow-left-light.svg' alt=''>
                </button>`,
    nextArrow: `<button class="product-page__slider-arrow product-page__slider-arrow--next"
                        aria-label="Next" type="button">
                  <img src='assets/img/arrow-right-light.svg' alt=''>
                </button>`,
    responsive: [{
      breakpoint: 575,
      settings: {
        dots: false,
        arrows: false,
        vertical: true,
        verticalSwiping: true,
        asNavFor: '.product-page__images-slider-nav'
      }
    }, {
      breakpoint: 767,
      settings: {
        dots: false,
        arrows: false,
        vertical: false,
        verticalSwiping: false,
        asNavFor: '.product-page__images-slider-nav'
      }
    }]
  });

  $('.product-page__images-slider-nav').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    asNavFor: '.product-page__images-slider',
    focusOnSelect: true,
    vertical: true,
    infinite: false,
    mobileFirst: true,
    verticalSwiping: true,
    prevArrow: `<button class="product-page__slider-arrow product-page__slider-arrow--prev" 
                        aria-label="Previous" type="button">
                  <img src='assets/img/arrow-left-light.svg' alt=''>
                </button>`,
    nextArrow: `<button class="product-page__slider-arrow product-page__slider-arrow--next"
                        aria-label="Next" type="button">
                  <img src='assets/img/arrow-right-light.svg' alt=''>
                </button>`,
    responsive: [{
      breakpoint: 638,
      settings: {
        slidesToShow: 4
      }
    }, {
      breakpoint: 767,
      settings: {
        vertical: false,
        verticalSwiping: false
      }
    }]       
  });

  document.querySelectorAll('[data-color-parameter]').forEach(item => {
    item.nextElementSibling.style.backgroundColor = item.getAttribute('data-color-parameter');
  });

  if (document.querySelector('.review-form__inner')) {
    document.querySelector('.review-form__inner').style.marginTop = '-' + document.querySelector('.review-form__inner').offsetHeight + 'px';     
  }

  let userName = 'Дмитрий';

  if (document.querySelector('.review__btn')) {
    document.querySelector('.review__btn').addEventListener('click', e => {
      e.preventDefault();
      if (e.target.classList.contains('active')) {
        const newReview = document.createElement('div');
        newReview.classList.add('review');
        newReview.innerHTML = `
        <div class="review">
          <div class="review__header">
            <div class="review__short">${document.querySelector('.review-form__input').value}</div>
            <div class="review__user">${userName}</div>
          </div>
          <div class="review__body">
            <p class="review__text">
            ${document.querySelector('.review-form__textarea').value}
            </p>
            <div class="review__rating">
              <form class="rating">
                <input type="radio" name="rating" value="1" checked>
                <input type="radio" name="rating" value="2" checked>
                <input type="radio" name="rating" value="3" checked>
                <input type="radio" name="rating" value="4">
                <input type="radio" name="rating" value="5">
              </form>
            </div>
          </div>
        </div>`;
        document.querySelector('.review-list').prepend(newReview);
        setTimeout(() => {
          document.querySelector('.review-form').reset();
          document.querySelector('.review-form__inner').style.marginTop = '-' + document.querySelector('.review-form__inner').offsetHeight + 'px';
          e.target.classList.remove('active')
        }, 300)
      } else {
        // if (document.querySelector('.review-form__inner')) {
          document.querySelector('.review-form__inner').style.marginTop = '0';          
        // }
        e.target.classList.add('active')
      }
    });
  }


  $('.product-page__products-slider').slick({
    slidesToShow: 3,
    slidesToScroll: 3,
    infinite: false,
    prevArrow: `<button class="product-page__slider-arrow product-page__slider-arrow--prev" 
                        aria-label="Previous" type="button">
                  <img src='assets/img/arrow-left-light.svg' alt=''>
                </button>`,
    nextArrow: `<button class="product-page__slider-arrow product-page__slider-arrow--next"
                        aria-label="Next" type="button">
                  <img src='assets/img/arrow-right-light.svg' alt=''>
                </button>`,
  });

  function infoModalShow() {
    document.querySelector('.info-modal').classList.add('show');
    document.body.classList.add('no-scroll');
  }
  
  function infoModalHide() {
    document.querySelector('.info-modal').classList.remove('show');
    document.body.classList.remove('no-scroll');
  }

 if (document.querySelector('.product-page__buy-link')) {
  document.querySelector('.product-page__buy-link').addEventListener('click', function() {
    infoModalShow();
  });
 }

  if (document.querySelector('.info-modal')) {
    document.querySelector('.info-modal').addEventListener('click', function(e) {
      if (e.target.classList.contains('info-modal')) {
        infoModalHide();
      } 
    });
  }

  if (document.querySelector('.info-modal__close-btn')) {
    document.querySelector('.info-modal__close-btn').addEventListener('click', function() {
      infoModalHide();
    });
  }

  if (document.querySelector('.order__btn')) {
    document.querySelector('.order__btn').addEventListener('click', (e) => {
      e.preventDefault();
      document.querySelector('.order__form').reset();
      window.location.href = 'success.html';
    });
  }

});
